        });
    },
    sendEmail() {
      if (this.email == "") {
        alert("Campo email es requerido");
      } else {
        axios.post('http://192.81.217.80:3000/questions/new-question', {email: this.email})
          .then((res) => {
            alert('Correo enviado')
          }).catch((err) => {
            console.log(this.email);
            console.log(err.response);
          });
      }
    },
    login() {
      if (this.user == "admin" && this.pass == "chocolate") {
        this.activateLogin = false;
      } else {
        alert("Login incorrecto");
      }
    }
  },
  created() {
    this.getData();
  }
};
</script>
